*,
::after,
::before {
  box-sizing: border-box;
}

body {
  height: 100%;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.5em;
  color: rgba(0, 0, 0, 0.87);
  background: #f5f5f5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App-container {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}